import { Component, OnInit } from '@angular/core';
import { PayrollPrintTemplateService, PayrollKomponenPerusahaanService, PayrollPrintTemplateDetailService, PayrollPrintTemplateDetailKomponenService } from 'src/app/sdk/payroll/services';
import { Print_template, Komponen_perusahaan, Print_template_detail } from 'src/app/sdk/payroll/models';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'app-add',
    templateUrl: './add.component.html',
    styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private activatedRoute: ActivatedRoute,
        private payrollPrintTemplateService: PayrollPrintTemplateService,
        private payrollPrintTemplateDetailService: PayrollPrintTemplateDetailService,
        private payrollPrintTemplateDetailKomponenService: PayrollPrintTemplateDetailKomponenService,
        private spinner: NgxSpinnerService,
        private router: Router,
    ) { }

    jenis_print: string = '';
    id_print_template: string = '';
    ngOnInit() {
        this.form();
        this.payrollPrintTemplateService.postPayrollPrintTemplates().subscribe(
            data => {
                this.id_print_template = data.data.id_print_template;
                this.print.setValue(data.data);
                this.getListItem()
            }
        )
    }

    print: FormGroupTypeSafe<Print_template>;
    form() {
        this.print = this.fb.group<Print_template>(
            {
                id_print_template: new FormControl('', [Validators.required]),
                module: new FormControl('', [Validators.required]),
                nama_template: new FormControl('', [Validators.required]),
                validasi: new FormControl(''),
                id_user: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl('')
            }
        )
    }

    getValueForm(fild, compare) {
        if (this.print.get(fild).value == compare) {
            return true;
        }
        return false;
    }

    simpan() {
        this.spinner.show()
        if (this.print.invalid) {
            console.log(this.print.invalid)
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }
        this.print.patchValue({ validasi: 1 });
        this.payrollPrintTemplateService.putPayrollPrintTemplatesId({ id: this.id_print_template, body: this.print.value }).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan, mohon cek Kode Komponen');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/payroll/module/pengaturan/template-slip'])
    }

    if_add: boolean = false;
    formItem: any = {}


    //List Komponen
    ListItem: any[] = [];
    getListItem() {
        this.spinner.show();
        this.payrollPrintTemplateDetailService.getPayrollPrintTemplateDetails(JSON.stringify({ id_print_template: this.id_print_template })).subscribe(
            data => {
                this.spinner.hide();
                this.ListItem = data.data

                this.ListItem.forEach((item, i) => {
                    this.ListItem[i].is_edit = false;
                })
            }
        )
    }


    listKomponenPerusahaan: Komponen_perusahaan[] = [];
    getListKomponenPerusahaan(untuk = null) {
        setTimeout(() => {

            console.log(this.formItem)
            this.formItem.id_componen = [];

            this.spinner.show()
            this.payrollPrintTemplateDetailKomponenService.getPayrollPrintTemplateDetailKomponens(JSON.stringify(
                {
                    jenis: this.formItem.jenis,
                    id_print_template: this.id_print_template,
                    selectIdKomponen: this.selectIdKomponen
                })).subscribe(
                    data => {
                        this.spinner.hide();
                        this.listKomponenPerusahaan = data.data;
                    }
                )
        }, 400);


    }

    selectIdKomponen = [];
    simpanItem() {
        this.spinner.show()
        if (this.formItem.nama_group == null || this.formItem.jenis == null || this.selectIdKomponen.length < 1) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.formItem.id_print_template = this.id_print_template
        this.formItem.id_komponen = this.selectIdKomponen
        this.payrollPrintTemplateDetailService.postPayrollPrintTemplateDetails(this.formItem).subscribe(
            data => {
                this.spinner.hide()
                this.selectIdKomponen = []
                this.getListItem()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan, mohon cek Kode Komponen');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )
    }

    edit(i) {
        console.log(i)
        this.selectIdKomponen = [];
        this.ListItem[i].is_edit = true;

        this.formItem.jenis = this.ListItem[i].jenis;
        this.ListItem[i].item.forEach(item => {
            this.selectIdKomponen.push(item.id_komponen_perusahaan)
        })

        this.getListKomponenPerusahaan();
    }

    simpanEdit(param) {
        this.spinner.show()
        param.id_komponen = this.selectIdKomponen;
        this.payrollPrintTemplateDetailService.putPayrollPrintTemplateDetailsId({ id: '00', body: param }).subscribe(
            data => {

                this.spinner.hide()
                this.getListItem()
            }
        )
    }

    delete(id_print_template_detail) {
        this.spinner.show()
        this.payrollPrintTemplateDetailService.deletePayrollPrintTemplateDetailsId(id_print_template_detail).subscribe(
            data => {
                this.spinner.hide()
                this.getListItem()
            }
        )
    }

}
