import { Component, OnInit } from '@angular/core';
import { PayrollPrintTemplateService } from 'src/app/sdk/payroll/services';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
    selector: 'app-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private pesan: PesanService,
        private activatedRoute: ActivatedRoute,
        private payrollPrintTemplateService: PayrollPrintTemplateService,
        private spinner: NgxSpinnerService,
        private router: Router,
    ) { }

    ngOnInit() {
        this.getList()
    }

    listTemplate: any[] = []
    getList() {
        this.spinner.show()
        this.payrollPrintTemplateService.getPayrollPrintTemplates().subscribe(
            data => {
                this.listTemplate = data.data

                this.spinner.hide()
            }
        )
    }

    delete(id) {
        this.spinner.show()
        this.payrollPrintTemplateService.deletePayrollPrintTemplatesId(id).subscribe(
            data => {
                this.pesan.pesanBerhasilForm(data.message);
                this.getList();
            },
            err => {
                this.pesan.pesanWarningForm('Template Ini Sudah digunakan!');
                this.spinner.hide()
            }
        )
    }

}
