import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { TemplateSlipRoutingModule } from './template-slip-routing.module';
import { ListComponent } from './list/list.component';
import { AddComponent } from './add/add.component';
import { EditComponent } from './edit/edit.component';
import { NzSelectModule, NgZorroAntdModule } from 'ng-zorro-antd';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';


@NgModule({
    declarations: [ListComponent, AddComponent, EditComponent],
    imports: [
        CommonModule,
        TemplateSlipRoutingModule,
        NzSelectModule,
        NgZorroAntdModule,
        ReactiveFormsModule,
        FormsModule
    ],
    providers: [
        FormBuilderTypeSafe
    ]

})
export class TemplateSlipModule { }
